.app-header {
  position: relative;
  display: flex;
  // justify-content: space-between;
  align-items: center;
  background-color: $clr-primary-1;
  color: $clr-w;
  padding: 0 $layoutPadding;
  font-size: 1.1rem;

  h1 {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5px;

    &::before {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      transform: skew(10deg, 0);
      box-shadow: -3px -2px 0px 2px $clr-y;
    }
  }

  nav {
    display: flex;
    height: 100%;
    margin-inline-start: calc(50vw - 190px);
    transform: translate(-35%);

    a {
      display: flex;
      align-items: center;
      padding: 20px 15px;

      &:hover {
        transition: background-color 0.1s;
        background-color: $clr-secondary;
      }

      &.active {
        background-color: $clr-secondary1;
        box-shadow: 0 4px 5px -4px black;

      }

    }

  }

  button {
    // margin-inline-start: calc(50vw - 190px);
    position: absolute;
    right: $layoutPadding;
  }
}