.multi-select {
    position: relative;

    .options-container {
        position: absolute;
        display: grid;
        grid-template-columns: 20px 1fr;
        align-items: center;
        &>* {
            margin-block: 5px;
        }

        background-color: $clr-w;
        border-block-end: 1px solid $clr-g-1;
        border-radius: 5px;
        padding: 10px;
        left: 0;

        // button {
        //     background-color: transparent;
        //     // border-radius: 50%;
        //     aspect-ratio: 1 / 1;
        //     border: 1px solid black;
        // }
    }
}