.toy-side {
    text-align: center;

    &>* {
        margin-block: 15px;
    }

    .toy-filter {
        &>* {
            font-size: 1.1rem;
        }

        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 10px 0;
        gap: 10px;
        border-block: 1px solid $clr-g-1;

        button {
            background-color: $clr-secondary;
            border: none;
            padding: 5px 10px;
            color: $clr-w;
            border-radius: 4px;

            &:hover {
                background-color: $clr-secondary-1;
            }
        }
    }
}