.toy-preview {
    max-width: 230px;
    border-radius: 5px;
    padding: 10px;
    
    &>* {
        margin-block-end: 10px;
    }
    
    &>:last-child {
        border-block-start: 2px solid $clr-g-1;
        padding-block-start: 10px;
        
    }
    .btn {
        font-size: 1em;
        margin-inline: 5px;
        background-color: $clr-secondary;

        &:hover {
            background-color: $clr-primary;
        }
    }
}