// Layout
$layoutPadding: 20px;



// Colors
$clr-primary: #2155CD;
$clr-primary1: lighten($clr-primary, 10%);
$clr-primary-1: darken($clr-primary, 5%);

$clr-secondary: #338be4;
$clr-secondary1: lighten($clr-secondary, 10%);
$clr-secondary-1: darken($clr-secondary, 10%);

$clr-b: #282c34;

$clr-g: #C8C6C6;
$clr-g-1: darken($clr-g, 10%);
$clr-g-2: darken($clr-g, 20%);
$clr-g1: lighten($clr-g, 10%);
$clr-g2: lighten($clr-g, 15%);


$clr-w: #fafafa;
$clr-w1: lighten($clr-w, 10%);
$clr-w-1: darken($clr-w, 10%);

$clr-y: #F9D923;
$clr-y1: lighten($clr-y, 10%);
$clr-y-1: darken($clr-y, 10%);